import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Touchable from '../../touchable/src/touchable';
import classNames from 'classnames';

const defaultProps = {
    value: 0,
    step: 1,
    min: -10000,
    max: 10000,
    decimalNum: 0,
    extraClass: '',
    disable: false,
    inputDisable: false,
    onChange() {
    }
};

const propTypes = {
    /**
     * @property value
     * @description 
     * @type Number
     * @default 0
     */
    value: PropTypes.number.isRequired,
    /**
     * @property step
     * @description 
     * @type Number
     * @default 1
     */
    step: PropTypes.number,
    /**
     * @property min
     * @description 
     * @type Number
     * @default -10000
     */
    min: PropTypes.number,
    /**
     * @property max
     * @description 
     * @type Number
     * @default 10000
     */
    max: PropTypes.number,
    /**
     * @property decimalNum
     * @description 
     * @type Number
     * @default 0
     */
    decimalNum: PropTypes.number,
    /**
     * @property extraClass
     * @description 
     * @type String
     */
    extraClass: PropTypes.string,
    /**
     * @property disable
     * @description 
     * @type Bool
     * @default false
     */
    disable: PropTypes.bool,
    /**
     * @property inputDisable
     * @description 
     * @type Bool
     * @default false
     */
    inputDisable: PropTypes.bool,
    /**
     * @property onChange
     * @description 
     * @param {Number} value 
     * @type Function
     * @default () => {}
     */
    onChange: PropTypes.func.isRequired
};

export default class Number extends Component {
    constructor(props) {
        super(props);
        const { value, step, min, max, decimalNum, disable, inputDisable } = props;
        this.state = {
            min,
            max,
            step,
            value,
            decimalNum,
            disable,
            inputDisable,
            plusDisable: false,
            minusDisable: false
        };
        this._node = null;
        if (isNaN(value)) {
            throw new Error('value');
        }
        this.cachedInput = value;
    }

    componentWillMount() {
        window.addEventListener('resize', this.resizeEvent.bind(this), false);
        this.resetState(this.state);
    }

    componentWillReceiveProps(nextProps) {
        const newState = Object.assign(
            {},
            this.state,
            nextProps,
            {
                plusDisable: false,
                minusDisable: false
            },
        );
        this.resetState(newState);
    }

    /**
     * value
     * :
     * valueNaNvalue
     * value>maxvalue<minmaxmin
     * value
     * @param val
     * @param min
     * @param max
     * @returns {Number}
     */
    getValidValue(val, min = this.props.min, max = this.props.max) {
        const value = parseFloat(val);
        if (isNaN(value)) {
            return parseFloat(this.cachedInput);
        } else if (value < min) {
            return min;
        } else if (value > max) {
            return max;
        }
        return value;
    }

    componentWillUnMount() {
        window.removeEventListener('resize', this.resizeEvent.bind(this), false);
    }

    resizeEvent() {
        this._node && this.wrapChange(this._node.value);
    }

    /**
     * Number
     * @param obj {Object} this.state
     */
    resetState(obj) {
        const stateObj = obj;
        stateObj.step = parseFloat(stateObj.step) || 0;
        stateObj.max = parseFloat(stateObj.max) || 0;
        stateObj.min = parseFloat(stateObj.min) || 0;
        const { disable, min, max, decimalNum } = stateObj;
        let { value } = stateObj;
        // 
        value = this.getValidValue(value, min, max).toFixed(decimalNum);

        //  
        if (disable) {
            this.setState({ value, disable, inputDisable: true, plusDisable: true, minusDisable: true });
            return;
        }
        // toFixed
        // 0.3min00input0
        // value0.30.3min0.3minminusDiablefalse
        // input0-
        if (parseFloat(value) === max) {
            stateObj.plusDisable = true;
        }
        if (parseFloat(value) === min) {
            stateObj.minusDisable = true;
        }
        stateObj.value = value;
        this.setState(stateObj);
    }

    /**
     * minusValue 
     * @param val {Number} inputvalue
     */
    minusValue(val) {
        if (this.state.minusDisable) {
            return;
        }
        let value = parseFloat(val) || 0;
        value -= this.state.step;
        this.wrapChange(value);
    }

    /**
     * plusValue  string'+'Number
     * @param val {String}
     */
    plusValue(val) {
        if (this.state.plusDisable) {
            return;
        }
        let value = parseFloat(val) || 0;
        value += this.state.step;
        this.wrapChange(value);
    }

    /**
     * onChangedecimalNum
     * domNum=0step=10.3
     * 0.30onChangevalue
     * aaagetValidValue
     * @param val
     * @returns {Number}
     */
    wrapChange(val) {
        const value = parseFloat(this.getValidValue(val).toFixed(this.state.decimalNum));
        this.props.onChange(value);
    }

    render() {
        const { inputDisable, plusDisable, minusDisable } = this.state;
        const minusDisableClass = minusDisable ? 'disabled' : '';
        const plusDisableClass = plusDisable ? 'disabled' : '';
        return (
            <div className={classNames('yo-number', this.props.extraClass)}>
                <Touchable
                    touchClass={minusDisable ? '' : 'action-touch'}
                    onTap={() => this._node && this.minusValue(this._node.value)}
                >
                    <span className={classNames('minus', minusDisableClass)}>-</span>
                </Touchable>
                <input
                    className="input" type="text" value={this.state.value}
                    disabled={inputDisable ? 'disabled' : ''}
                    ref={target => {
                        this._node = target;
                    }}
                    onChange={evt => {
                        // blur
                        this.setState({ value: evt.target.value });
                    }}
                    onBlur={evt => {
                        this.wrapChange(evt.target.value);
                    }}
                    onFocus={evt => {
                        // valuevalue
                        // value
                        // +-
                        this.cachedInput = evt.target.value;
                        this.setState({ plusDisable: true, minusDisable: true });
                    }}
                />
                <Touchable
                    touchClass={plusDisable ? '' : 'action-touch'}
                    onTap={() => this._node && this.plusValue(this._node.value)}
                >
                    <span className={classNames('plus', plusDisableClass)}>+</span>
                </Touchable>
            </div>
        );
    }
}

Number.defaultProps = defaultProps;
Number.propTypes = propTypes;



