/**
 * @component ActionSheet
 * @version 3.0.0
 * @description Popup
 *
 * - iOSAPI
 * - 
 *
 * @instructions {instruInfo: ./actionSheet.md}{instruUrl: actionsheet.html?hideIcon}
 */
import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import Touchable from '../../touchable/src/touchable';
import Popup from '../../popup/src/popup';

let that = null;
const container = document.createElement('div');

class ActionSheet extends Component {
    constructor() {
        super();
        this.state = {
            show: false,
            cancelText: '',
            menu: [],
            title: ''
        };
        that = this;
    }

    hide() {
        this.setState({
            show: false
        });
    }

    render() {
        const { show, cancelText, menu, title } = this.state;

        const menuItem = menu.map((item, i) => (
            <Touchable
                onTap={() => {
                    this.hide();
                    item.onTap();
                }}
                key={i + 1}
                touchClass="item-touch"
            >
                <div className="item">{item.text}</div>
            </Touchable>
        ));

        const titleItem = !!title ? (<div className="title" key={0}>{title}</div>) : null;
        menuItem.unshift(titleItem);
        return (
            <Popup
                show={show}
                onMaskTap={() => this.hide()}
            >
                <div className="yo-actionsheet">
                    <div className="menu">
                        {menuItem}
                    </div>
                    <ul className="action">
                        <Touchable onTap={() => this.hide()} touchClass="item-touch">
                            <li
                                className="item"
                                onTouchTap={() => this.hide()}
                            >{cancelText}</li>
                        </Touchable>
                    </ul>
                </div>
            </Popup>
        );
    }
}

ReactDOM.render(<ActionSheet />, container);

/**
 * @method ActionSheet
 * @param {Object} obj 
 * @param {Array} obj.menu 
 * @param {String} [obj.title] 
 * @param {String} [obj.cancelText] ''
 * @description ActionSheet
 */
export default ({ menu, title = '', cancelText = '' }) => that.setState({
    show: true,
    menu,
    title,
    cancelText
});


