import FastClick from './fastclick';
import injectTapEventPlugin from 'react-tap-event-plugin';
import { supportsPassiveOption } from './util';
import 'raf/polyfill';

if (!window.___yoTapEventInjected) {
    // 
    // yo-routertap-event-plugintry catchtap-event-plugin
    try {
        injectTapEventPlugin();
    } catch (e) {

    }

    if (document.readyState === 'complete'
        || document.readyState === 'interactive') {
        FastClick.attach(document.body);
    }

    document.addEventListener('DOMContentLoaded', () => {
        FastClick.attach(document.body);
    });

    document.body.addEventListener('touchmove', (evt) => {
        evt.preventDefault();
    }, supportsPassiveOption() ? {
        passive: false
    } : false);
    //  iOS 11.3  root document  touch  passive 
    //  preventDefault  passive  false
    window.___yoTapEventInjected = true;
}


