/**
 * ,,
 * API
 */

export default class LoopedArray {

    constructor(dataSource) {
        this.dataSource = dataSource;
    }

    /**
     * i,
     * @param index
     * @param dataSource
     * @returns {item}
     */
    getItem(index, dataSource = this.dataSource) {
        return JSON.parse(JSON.stringify(dataSource[index % dataSource.length]));
    }

    /**
     * slice,
     * @param start
     * @param end
     * @returns {Array}
     */
    slice(start, end) {
        const ret = [];
        for (let i = start; i < end; i++) {
            ret.push(this.getItem(i));
        }
        return ret;
    }

    /**
     * map,
     * operationgetItemgetItem
     * @param operation
     * @returns {LoopedArray}
     */
    map(operation) {
        const ret = new LoopedArray([]);
        ret.getItem = (index) => operation(this.getItem(index), index);
        return ret;
    }

    /**
     * filter,
     * @param prediction
     * @returns {Array}
     */
    filter(prediction) {
        return new LoopedArray(this.dataSource.filter(prediction));
    }
}



