import yoConfirm from '../../confirm/src/confirm';

/**
 * @method Alert
 * @description Alert APIAlertoption
 *  ``Alert(content, title, btnText, animation, extraClass)``
 * @param {Object} option 
 * @param {String | Function} [option.content]  jsx jsx `() => jsx`
 * @param {String} [option.title] 
 * @param {String} [option.btnText] <3.0.1> 
 * @param {String | Object} [option.animation] Dialog
 * @param {String} [option.extraClass] <3.0.15> className
 * @constructor Alert API
 */
export default function Alert(content = '', title = '', btnText = ['', ''], animation = 'fade', extraClass = '') {
    if (typeof content === 'object') {
        const opt = content;
        content = opt.content != null ? opt.content : '';
        title = opt.title != null ? opt.title : '';
        btnText = opt.btnText != null ? [opt.btnText, ''] : ['', ''];
        animation = opt.animation || 'fade';
        extraClass = opt.extraClass != null ? opt.extraClass : '';
    }
    return yoConfirm(content, title, btnText, animation, false, extraClass);
}



