import React, { Component } from 'react';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import List from '../../list/src/list';
import GroupCore from './GroupCore';
import {
    inheritProps,
    DELAY_TIME_FOR_INFINITE_WITHOUT_HEIGHT
} from '../../common/util';
import classNames from 'classnames';
import IndexNavBar from './IndexNavBar';

const propTypes = {
    /**
     * @property dataSource
     * @type Array/Immutable List
     * @default null
     * @description groupKey(String)groupKey'notGrouped'
     *
     * :
     * ```
     * [
     *   {
     *     text: String //renderItemtext
     *     groupKey: String //groupKey,'notGrouped'
     *   },
     *   ...
     * ]
     * ```
     */
    dataSource: PropTypes.oneOfType([PropTypes.arrayOf(
        PropTypes.shape({
            text: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
            groupKey: PropTypes.oneOfType([PropTypes.number, PropTypes.string])
        })
    ), PropTypes.object]).isRequired,
    /**
     * @property sort
     * @type Function
     * @default null
     * @param {String} a title
     * @param {String} b title
     * @description array.sortab
     * abab
     */
    sort: PropTypes.func,
    /**
     * @property renderGroupTitle
     * @type Function
     * @default (groupKey)=>groupKey
     * @param groupKey {String} titlegroupKey
     * @description groupKeygroup title
     */
    renderGroupTitle: PropTypes.func,
    /**
     * @property renderGroupItem
     * @type Function
     * @param {Object} item 
     * @param {Number} index index
     * @default item=>item.text
     * @description JSXtext()
     */
    renderGroupItem: PropTypes.func,
    /**
     * @property staticSection
     * @type Element
     * @default null
     * @version 3.0.3
     * @description Infinite
     * staticSectionstaticSectionHeight
     */
    staticSection: PropTypes.element,
    /**
     * @property staticSectionHeight
     * @type Number
     * @version 3.0.3
     * @default 0
     * @description staticSection
     */
    staticSectionHeight: PropTypes.number,
    /**
     * @property showIndexNavBar
     * @type Bool
     * @default false
     * @description 
     */
    showIndexNavBar: PropTypes.bool,
    /**
     * @property infinite
     * @type Bool
     * @default false
     * @description (List)
     */
    infinite: PropTypes.bool,
    /**
     * @property infiniteSize
     * @type Number
     * @default 30
     * @description 
     * grouplist30
     */
    infiniteSize: PropTypes.number,
    /**
     * @property itemHeight
     * @type Number
     * @default null
     * @description 
     */
    itemHeight: PropTypes.number,
    /**
     * @property titleHeight
     * @type Number
     * @default 25
     * @description group titleinfinitetitleList
     */
    titleHeight: PropTypes.number,
    /**
     * @property titleOffset
     * @type Number
     * @default 0,
     * @description group titletop:0GroupList
     * @version 3.0.6
     */
    titleOffset: PropTypes.number,
    /**
     * @property itemExtraClass
     * @type String/Function
     * @default null
     * @param {Object} item 
     * @param {Number} index 
     * @description grouplistextraClassListitemExtraClass
     *
     * :/className
     */
    itemExtraClass: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
    /**
     * @property groupTitleExtraClass
     * @type String
     * @default null
     * @param {String} groupKey 
     * @description grouptitleextraClassgroupKey
     *
     * :/className
     */
    groupTitleExtraClass: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
    /**
     * @property offsetY
     * @type Number
     * @default 0
     * @description 
     */
    offsetY: PropTypes.number,
    /**
     * 
     *
     * @property contentInset
     * @type Number
     * @description ** bottom** iPhoneX
     * @default {bottom:0}
     * @version 3.0.13
     */
    contentInset: PropTypes.shape({
        bottom: PropTypes.number
    }),
    /**
     * @property onItemTap
     * @type Function
     * @default ()=>{}
     * @param {Object} item 
     * @param {Number} index 
     * @param {element} target eventtarget
     * @description List
     */
    onItemTap: PropTypes.func,
    /**
     * @property itemTouchClass
     * @type String/Function
     * @param {Object} item 
     * @param {Number} index 
     * @default item-light
     * @description classNameList
     */
    itemTouchClass: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
    /**
     * @property usePullRefresh
     * @type Bool
     * @description 
     */
    usePullRefresh: PropTypes.bool,
    /**
     * 
     *
     * @property pullRefreshHeight
     * @type Number
     * @description 
     * @default 40
     */
    pullRefreshHeight: PropTypes.number,
    /**
     * 
     *
     * @property renderPullRefresh
     * @type Function
     * @returns {JSX}  pullRefresh  JSX
     * @description () => JSX
     *
     * 
     */
    renderPullRefresh: PropTypes.func,
    /**
     * @property onRefresh
     * @type Function
     * @param {Array} dataSource 
     * @description 
     */
    onRefresh: PropTypes.func,

    /**
     * @property useLoadMore
     * @type Bool
     * @description 
     */
    useLoadMore: PropTypes.bool,
    /**
     * @property onLoad
     * @type Function
     * @description 
     */
    onLoad: PropTypes.func,
    /**
     * 
     *
     * @property loadMoreHeight
     * @type Number
     * @description 
     * @default 40
     */
    loadMoreHeight: PropTypes.number,
    /**
     * @property renderLoadMore
     * @type Function
     * @return JSX
     * @description JSX
     */
    renderLoadMore: PropTypes.func,
    /**
     * @property onScroll
     * @type Function
     * @param {Number} offsetY  Scroller  y 
     * @description Scroller
     */
    onScroll: PropTypes.func,
    /**
     * @property onScrollEnd
     * @type Function
     * @param {Number} offsetY  Scroller  y 
     * @default null
     * @version 3.1.4
     * @description 
     */
    onScrollEnd: PropTypes.func,
    /**
     * @property extraClass
     * @type String
     * @description className
     */
    extraClass: PropTypes.string,
    /**
     * @property style
     * @type Object
     * @default null
     * @description GroupList
     * @version 3.0.2
     */
    style: PropTypes.object,
    shouldItemUpdate: PropTypes.func,
    /**
     * @property onIndexNavBarItemHover
     * @type Function
     * @param groupKey {String} navBarItemgroupKey
     * @description 
     */
    onIndexNavBarItemHover: PropTypes.func,
    /**
     * @property renderIndexNavBarItem
     * @type Function
     * @param {String/Number} groupKey
     * @default groupKey=>groupKey
     * @description grouplistrendergroupkeyjsx
     */
    renderIndexNavBarItem: PropTypes.func,
    /**
     * @property disabled
     * @type Bool
     * @default false
     * @description ,Scroller
     * @version 3.0.2
     */
    disabled: PropTypes.bool,
    /**
     * 
     *
     * @property directionLockThreshold
     * @type Number
     * @description 
     * @default 5
     * @version 3.0.2
     */
    directionLockThreshold: PropTypes.number,
    /**
     * @property deceleration
     * @type Number
     * @description 0.010
     * @version 3.0.6
     */
    deceleration: PropTypes.number,
    /**
     * @property scrollWithoutTouchStart
     * @type Bool
     * @default false
     * @description **  **
     * scrollToscrollertouchstartscrollerdisableenable
     * touchstart
     * truescrollertouchmove
     * @version 3.0.2
     */
    scrollWithoutTouchStart: PropTypes.bool,
    /**
     * @skip
     * @property isTitleStatic
     * @description render
     */
    isTitleStatic: PropTypes.bool,
    /**
     * @property stickyOffset
     * @type Number
     * @default 0
     * @description staticSectiony
     * @version 3.0.6
     */
    stickyOffset: PropTypes.number
};

const defaultProps = {
    // item,infinite
    itemHeight: null,
    // ,infinite
    titleHeight: 25,
    // 
    infinite: false,
    // ,infinite
    infiniteSize: 30,
    // title renderer
    renderGroupTitle(groupKey) {
        return groupKey;
    },
    // item renderer
    renderGroupItem(item) {
        return typeof item.get === 'function' ? item.get('text') : item.text;
    },
    // className
    extraClass: '',
    // 
    showIndexNavBar: false,
    // item renderer
    renderIndexNavBarItem(groupKey) {
        return groupKey;
    },
    groupTitleExtraClass: '',
    offsetY: 0,
    onItemTap: null,
    sort: null,
    itemTouchClass: 'item-touch',
    usePullRefresh: false,
    useLoadMore: false,
    onRefresh() {
    },
    itemExtraClass: '',
    onIndexNavBarItemHover() {
    },
    onScroll() {
    },
    shouldItemUpdate() {
        return false;
    },
    disabled: false,
    style: null,
    scrollWithoutTouchStart: true,
    staticSection: null,
    isTitleStatic: false,
    titleOffset: 0
};

export default class GroupList extends Component {

    /**
     * statedataSource(title)groupTitle
     * @param props
     */
    constructor(props) {
        super(props);
        const {
            dataSource,
            itemHeight,
            titleHeight,
            sort,
            infinite,
            isTitleStatic,
            titleOffset
        } = this.props;
        this.groupModel = new GroupCore({
            dataSource,
            itemHeight,
            titleHeight,
            sort,
            infinite,
            isTitleStatic,
            titleOffset
        });
        this.state = {
            dataSource: this.groupModel.dataSource,
            groupTitles: this.groupModel.groupTitles
        };
    }

    componentDidMount() {
        // stickyHeaderdisplay
        this.groupModel
            .registerEventHandler('refreshStickyHeader', (stickyHeader) => {
                if (stickyHeader.title) {
                    // sticky headersetState,render
                    // ,dom
                    const { groupKey } = stickyHeader.title,
                        { offset } = stickyHeader,
                        transform = `translate(0px,${offset}px) translateZ(0px)`,
                        groupTitle = this.props.renderGroupTitle(stickyHeader.title.groupKey),
                        { groupTitleExtraClass } = this.props;

                    if (typeof groupTitle === 'string' || typeof groupTitle === 'number' || groupTitle === null) {
                        if (this.stickyHeader) { this.stickyHeader.innerHTML = groupTitle; }
                    } else {
                        if (this.stickyHeader) { ReactDOM.render(groupTitle, this.stickyHeader); }
                    }
                    if (this.stickyHeader) {
                        this.stickyHeader.style.tranform = transform;
                        this.stickyHeader.style.webkitTransform = transform;
                        this.stickyHeader.style.display = stickyHeader.title.display;
                        this.stickyHeader.className = `${(typeof groupTitleExtraClass === 'function' ? groupTitleExtraClass(groupKey) : groupTitleExtraClass)} sticky label group-title`;
                    }
                } else {
                    if (this.stickyHeader) { this.stickyHeader.style.display = 'none'; }
                }
            })
            .registerEventHandler('refresh', (dataSource, groupTitles) => {
                this.setState({ dataSource, groupTitles });
            });

        if (this.groupModel.isHeightFixed) {
            this.refreshStaticSectionHeight();
            this.refreshStickyHeader();
        } else {
            setTimeout(() => {
                this.refreshStaticSectionHeight();
                this.refreshStickyHeader();
            }, DELAY_TIME_FOR_INFINITE_WITHOUT_HEIGHT);
        }
    }

    componentWillReceiveProps(nextProps) {
        const { dataSource, sort, infinite, titleHeight, titleOffset } = nextProps;
        this.groupModel.refresh({ dataSource, sort, infinite, titleHeight, titleOffset });

        setTimeout(() => {
            this.refreshStaticSectionHeight();
            this.refreshStickyHeader();
        }, 0);
    }

    /**
     * offsetYstickyHeader
     * @param offsetY
     */
    refreshStickyHeader(offsetY = this.groupModel.offsetY) {
        this.groupModel.offsetY = offsetY;
        this.groupModel.refreshStickyHeader(offsetY);
    }

    refreshStaticSectionHeight() {
        if (this.list && this.list.staticSectionContaienr != null) {
            this.groupModel.staticSectionHeight = this.list.staticSectionContaienr.offsetHeight;
        }
    }

    /**
     * ,
     * @param groupKey
     */
    scrollToGroup(groupKey) {
        this.stopAnimate();
        if (this.list) {
            let targetOffsetY = -this.groupModel.getGroupOffsetY(groupKey);
            const maxScrollY = this.list.scroller.maxScrollY;
            targetOffsetY = targetOffsetY < maxScrollY ? maxScrollY : targetOffsetY;

            this.list.scrollTo(targetOffsetY, 0);
        }
    }

    /**
     * @method refresh
     * @description GroupListScroller
     * @version 3.0.6
     */
    refresh() {
        // grouplistmodalmodalgrouplistmount
        // onShowtitlecomponentDidMount
        this.forceUpdate();
        this.refreshStickyHeader();
        if (this.list) { this.list.refresh(); }
    }

    /**
     * @method resetLoadStatus
     * @param {Bool} hasLoadMore false onLoadMore
     * @description 
     * @version 3.0.7
     */
    resetLoadStatus(hasLoadMore) {
        if (this.list) { this.list.resetLoadStatus(hasLoadMore); }
    }

    /**
     * @description List,y
     * @method scrollTo
     * @param {Number} y y
     * @param {Number} [time] 
     */
    scrollTo(y, time) {
        if (this.list) { this.list.scrollTo(y, time); }
    }

    /**
     * @description List,
     * @method stopAnimate
     */
    stopAnimate() {
        if (this.list) { this.list.stopAnimate(); }
    }

    /**
     * @method stopRefreshing
     * @param {Bool} [success]  false
     * @param {Object} [config] 
     * @param {number} [config.duration]  300ms
     * @param {Function} [callback] 
     * @description  Scroller (),
     */
    stopRefreshing(success, config, callback) {
        if (this.list) {
            this.list.stopRefreshing(success, config, callback);
        }
    }

    /**
     * @description List,
     * @method startRefreshing
     */
    startRefreshing() {
        if (this.list) { this.list.startRefreshing(); }
    }

    /**
     * @method stopLoading
     * @param {Bool} [success] 
     * @description List,
     */
    stopLoading(success) {
        if (this.list) { this.list.stopLoading(success); }
    }

    /**
     * item._typetitle/groupitem
     * @param item
     * @returns {*}
     */
    renderItem(item, index) {
        const { renderGroupTitle, renderGroupItem } = this.props;
        return item._type === 'groupTitle' ? renderGroupTitle(item.groupKey, index) : renderGroupItem(item.srcData, index);
    }

    render() {
        const {
            style,
            itemExtraClass,
            groupTitleExtraClass,
            extraClass,
            onItemTap,
            renderIndexNavBarItem,
            itemTouchClass,
            onRefresh,
            onIndexNavBarItemHover,
            onScroll,
            shouldItemUpdate,
            titleHeight,
            titleOffset
        } = this.props;
        // showIndexNavBar,item_translateY
        const showIndexNavBar = this.props.showIndexNavBar && this.groupModel.isHeightFixed;
        const rootClassNames = classNames('yo-group', extraClass);
        const { dataSource, groupTitles } = this.state;
        // propsonItemTap,grouplisttitle
        // onItemTapfiltertitle
        const wrappedonItemTap = (item, index, target) => {
            if (onItemTap && item._type !== 'groupTitle') {
                onItemTap(item.srcData, index, target);
            }
        };
        // 
        const wrappedOnPullRefresh = (ds) => {
            onRefresh(ds.filter((item) => item._type !== 'groupTitle'));
        };
        const wrappedItemActiveClass = (item, index) => {
            let ret = null;
            if (item._type === 'groupTitle') {
                ret = null;
            } else {
                ret = typeof itemTouchClass !== 'function' ? itemTouchClass : itemTouchClass(item.srcData, index);
            }
            return ret;
        };
        const wrappedItemExtraClass = (item, index) => {
            let ret = null;
            if (item._type === 'groupTitle') {
                ret = typeof groupTitleExtraClass !== 'function' ? groupTitleExtraClass : groupTitleExtraClass(item.groupKey);
            } else {
                ret = typeof itemExtraClass !== 'function' ? itemExtraClass : itemExtraClass(item.srcData, index);
            }
            return ret;
        };
        const wrappedShouldItemUpdate = (next, now) => {
            if (!(now._type === 'groupTitle' && next._type === 'groupTitle')) {
                return shouldItemUpdate(next.srcData, now.srcData);
            }
            return false;
        };

        return (
            <div className={rootClassNames} style={style}>
                <div
                    className="sticky label"
                    style={{ height: titleHeight, top: titleOffset, display: 'none' }}
                    ref={(dom) => {
                        if (dom) {
                            this.stickyHeader = dom;
                        }
                    }}
                />
                {showIndexNavBar ? <IndexNavBar
                    list={groupTitles}
                    renderItem={renderIndexNavBarItem}
                    onNavItemFocus={(item) => {
                        if (item) {
                            this.scrollToGroup(item.groupKey);
                            onIndexNavBarItemHover(item.groupKey);
                        }
                    }}
                /> : null}
                <List
                    {...inheritProps(this.props, [
                        'scrollWithoutTouchStart',
                        'directionLockThreshold',
                        'disabled',
                        'infinite',
                        'infiniteSize',
                        'offsetY',
                        'usePullRefresh',
                        'pullRefreshHeight',
                        'renderPullRefresh',
                        'useLoadMore',
                        'onLoad',
                        'onScrollEnd',
                        'renderLoadMore',
                        'loadMoreHeight',
                        'staticSection',
                        'deceleration',
                        'stickyOffset',
                        'contentInset'
                    ])}
                    dataSource={dataSource}
                    extraClass="yo-scroller-fullscreen"
                    renderItem={(item, index) => this.renderItem(item, index)}
                    itemExtraClass={wrappedItemExtraClass}
                    shouldItemUpdate={wrappedShouldItemUpdate}
                    onScroll={(y, direction) => {
                        this.refreshStickyHeader(-y);
                        onScroll(y, direction);
                    }}
                    onListItemUpdate={(item, domNode) => {
                        this.groupModel.updateGroupTitle(item, domNode);
                    }}
                    onItemTap={wrappedonItemTap}
                    itemTouchClass={wrappedItemActiveClass}
                    ref={(list) => {
                        if (list) {
                            this.list = list;
                        }
                    }}
                    onRefresh={wrappedOnPullRefresh}
                />
            </div>
        );
    }
}

GroupList.propTypes = propTypes;
GroupList.defaultProps = defaultProps;



