import Modal from '../../modal/src/modal';
import React, { Component } from 'react';
import PropTypes from 'prop-types';

const noop = () => {
};

const propTypes = {
    /**
     * @property show
     * @type Bool
     * @description 
     * @default false
     */
    show: PropTypes.bool,
    /**
     * @property duration
     * @type Number
     * @description ms
     * @default 200ms
     */
    duration: PropTypes.number,
    /**
     * @property height
     * @type String/Number
     * @description 
     * @default 'auto'
     */
    height: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    /**
     * @property direction
     * @type Enum {'up', 'down'}
     * @description 
     * @default 'up'
     */
    direction: PropTypes.oneOf(['up', 'down']),
    /**
     * @method onMaskTap
     * @type Function
     * @description 
     * @default () => {}
     */
    onMaskTap: PropTypes.func,
    /**
     * @method onShow
     * @type Function
     * @description 
     * @default () => {}
     */
    onShow: PropTypes.func,
    /**
     * @method onHide
     * @type Function
     * @description 
     * @default () => {}
     */
    onHide: PropTypes.func,
    /**
     * @property maskOffset
     * @type Array<Number>
     * @description 
     * @default [0, 0]
     */
    maskOffset: PropTypes.arrayOf(PropTypes.number),
    /**
     * @property extraClass
     * @type String
     * @description class
     */
    extraClass: PropTypes.string,
    /**
     * @property wrapperExtraClass
     * @type String
     * @version 3.0.10
     * @description ()class
     */
    wrapperExtraClass: PropTypes.string,
    delayBeforeAnimationStart: PropTypes.number,
    children: PropTypes.oneOfType([PropTypes.object, PropTypes.array])
};

const defaultPros = {
    duration: 200,
    height: 'auto',
    direction: 'up',
    onMaskTap: noop,
    onShow: noop,
    onHide: noop
};

export default class Popup extends Component {
    render() {
        const {
            show,
            duration,
            height,
            direction,
            onMaskTap,
            onShow,
            onHide,
            maskOffset,
            extraClass,
            wrapperExtraClass,
            children,
            delayBeforeAnimationStart
        } = this.props;
        const animation = direction === 'up' ?
            ['fade-in-up', 'fade-out-down'] : ['fade-in-down', 'fade-out-up'];
        const align = direction === 'up' ? 'bottom' : 'top';
        return (
            <Modal
                show={show}
                height={height}
                width={'100%'}
                direction={direction}
                onMaskTap={onMaskTap}
                onShow={onShow}
                onHide={onHide}
                align={align}
                animation={{ animation, duration }}
                maskOffset={maskOffset}
                extraClass={wrapperExtraClass}
                contentExtraClass={extraClass}
                delayBeforeAnimationStart={delayBeforeAnimationStart}
                //  onTouchStart 
                onTouchStart={(evt) => {
                    evt.stopPropagation();
                }}
                onTouchMove={(evt) => {
                    evt.stopPropagation();
                }}
                onTouchEnd={(evt) => {
                    evt.stopPropagation();
                }}
                onTouchCancel={(evt) => {
                    evt.stopPropagation();
                }}
            >
                {children}
            </Modal>
        );
    }
}

Popup.propTypes = propTypes;
Popup.defaultProps = defaultPros;


