/**
 * @component LazyImage
 * @author jiao.shen
 * @description  `Scroller`  `List` 
 *
 * imgList
 * @instructions {instruInfo: ./lazyimage.md}{instruUrl: scroller/lazyimage.html?hideIcon}
 * @version  3.0.2
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
    getElementOffsetY,
    inheritProps
} from '../common/util';

const TO_BE_LOADED = 0;
const LOADING = 1;
const LOADED = 2;

export default class extends Component {
    static contextTypes = {
        // context
        // list/scroller
        list: PropTypes.object,
        scroller: PropTypes.object,
        // listitemoffsetY(infinite)
        offsetY: PropTypes.number,
        // listitem
        itemRef: PropTypes.object,
        // ScrollerLazyload,List
        isScroller: PropTypes.bool,
        // infinite
        infinite: PropTypes.bool
    };

    static propTypes = {
        /**
         * @property defaultImage
         * @type String
         * @default null
         * @description 
         */
        defaultImage: PropTypes.string,
        /**
         * @property src
         * @type String
         * @default null
         * @description src
         */
        src: PropTypes.string.isRequired,
        /**
         * @property className
         * @type String
         * @default null
         * @description img
         */
        className: PropTypes.string,
        /**
         * @property width
         * @type Number
         * @default null
         * @description 
         */
        width: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
        /**
         * @property height
         * @type Number
         * @default null
         * @description 
         */
        height: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
        /**
         * @property customAttr
         * @type Object
         * @default null
         * @description img domdata-
         */
        customAttr: PropTypes.object,
        /**
         * @property style
         * @type Object
         * @default null
         * @description img domstyle
         */
        style: PropTypes.object,
        /**
         * @property alt
         * @type String
         * @default null
         * @description imgalt
         */
        alt: PropTypes.string,
        /**
         * @property title
         * @type String
         * @default null
         * @description imgtitle
         */
        title: PropTypes.string,
        onTouchStart: PropTypes.func,
        onTouchMove: PropTypes.func,
        onTouchEnd: PropTypes.func,
        onTouchCancel: PropTypes.func
    };

    static defaultProps = {
        defaultImage: null,
        src: null,
        className: null,
        width: null,
        height: null,
        customAttr: {},
        style: null
    };

    constructor(props) {
        super(props);
        // 0->load,1->loading,2->loaded
        this.loading = TO_BE_LOADED;
        this.state = {
            src: this.props.defaultImage
        };
    }

    componentDidMount() {
        const self = this;
        this.refresh(this.context);
        function loadImage(scroller) {
            if (scroller.loadImage) {
                setTimeout(() => {
                    scroller.loadImage(self);
                }, 0);
            }
        }
        // list  lazyimage
        //  context  itemRef list item  lazyimage list.childLazyImages 
        //  context  itemRef list staticSection  lazyimage list.scroller.childLazyImages 
        if (this.context.list) {
            const list = this.context.list;
            const itemRef = this.context.itemRef;
            if (itemRef) {
                list.childLazyImages.push(this);
                loadImage(list);
            } else {
                setTimeout(() => {
                    if (list.scroller) {
                        list.scroller.childLazyImages.push(self);
                        loadImage(list.scroller);
                    }
                }, 0);
            }
        } else if (this.context.scroller) {
            const scroller = this.context.scroller;
            scroller.childLazyImages.push(this);
            loadImage(scroller);
        }
    }

    // render,loadedcontext
    componentWillReceiveProps(nextProps) {
        //  refresh  didupdate 
        // this.refresh(nextContext);
        this._isUpdate = true;

        if (this.state.src !== nextProps.src) {
            this.loading = TO_BE_LOADED;
            this.setState({ src: this.props.defaultImage });
        }
    }

    componentDidUpdate() {
        if (this._isUpdate) {
            this._isUpdate = false;

            this.refresh(this.context);
        }
    }

    componentWillUnmount() {
        const scroller = this.context.list || this.context.scroller;
        if (scroller) {
            scroller.childLazyImages.splice(scroller.childLazyImages.indexOf(this), 1);
        }
        this.canLoadImage = false;
    }

    refresh(context) {
        this.canLoadImage = true;
        this.offsetY = context.offsetY;
        this.itemRef = context.itemRef;
        this.infinite = context.infinite;
        // infiniteoffsetTop()translateY
        //  infinite  true list  staticSection  lazyimage  offsetTop
        if ((!this.infinite && this.img) || (this.infinite && !this.itemRef)) {
            this.offsetTop = getElementOffsetY(this.img);
            this.height = this.img.offsetHeight;
        }
    }

    load(callback) {
        if (this.loading === TO_BE_LOADED) {
            const { src } = this.props,
                tmpImg = new Image();
            this.loading = LOADING;

            tmpImg.onload = () => {
                // lazyimageunmount(SPAview)
                // 
                // bugfix: lazyimage
                // 
                if (this && this.canLoadImage && src === this.props.src) {
                    this.loading = LOADED;
                    this.setState({ src, loaded: true });
                    if (callback) {
                        callback();
                    }
                }
            };
            tmpImg.src = src;
        }
    }

    render() {
        const { height, style, customAttr } = this.props;
        if (this.context.list) {
            if (height == null && style.height == null) {
                throw Error('yo-lazyimage: ListLazyImage');
            }
        }

        return (
            <img
                {...inheritProps(this.props, [
                    'onTouchStart',
                    'onTouchMove',
                    'onTouchEnd',
                    'onTouchCancel',
                    'width',
                    'height',
                    'className',
                    'title',
                    'style'
                ])}
                alt={this.props.alt}
                ref={(img) => {
                    if (img) this.img = img;
                }}
                src={this.state.src}
                {...customAttr}
            />
        );
    }
}



