/**
 * @component PopupPicker
 * @version 3.0.5
 * @description `Popup``Picker`Picker
 *
 * @instructions {instruInfo: ./popuppicker.md}{instruUrl: popuppicker.html?hideIcon}
 * @author tianqi.tian
 */
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import classNames from 'classnames';
import Touchable from '../../touchable/src/touchable';
import Popup from '../../popup/src/popup';
import Picker from '../../picker/src/picker';
import { isFunction } from '../../common/util';

const propTypes = {
    /**
     * @property touchClass
     * @type String
     * @default null
     * @description  className
     */
    touchClass: PropTypes.string.isRequired,
    /**
     * @property value
     * @type Number/String/Array <Number, String>
     * @default null
     * @description  value`select` value 
     *
     * value  onChange  value 
     *
     * optionsvalue
     * valueoptionslengthvaluenull
     */
    value: PropTypes.oneOfType([
        PropTypes.number,
        PropTypes.string,
        PropTypes.arrayOf(PropTypes.oneOfType([PropTypes.string, PropTypes.number]))
    ]),
    /**
     * @property onChange
     * @type Function
     * @param value  option  valuevalue
     * @description  Ok 
     */
    onChange: PropTypes.func.isRequired,
    /**
     * @property onSelect
     * @type Function
     * @param value  option  valuevalue
     * @version 3.0.16
     * @description 
     */
    onSelect: PropTypes.func,
    /**
     * @property options
     * @type Array/Array <Array>
     * @default null
     * @description `Picker` options `{text:string,value:string}`
     *
     * text  option value  option  option 
     *
     * text  valuevalue /
     *
     * PopupPickerPickeroption
     */
    options: PropTypes.oneOfType([PropTypes.arrayOf(
        PropTypes.shape(
            {
                text: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
                value: PropTypes.oneOfType([PropTypes.number, PropTypes.string]).isRequired
            }
        )
    ).isRequired, PropTypes.arrayOf(
        PropTypes.arrayOf(
            PropTypes.shape(
                {
                    text: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
                    value: PropTypes.oneOfType([PropTypes.number, PropTypes.string]).isRequired
                }
            )
        )
    )]),
    /**
     * @property popupHeader
     * @type Object
     * @default {
     *   title: '',
     *   okBtn: { text: '', touchClass: 'action-touch' },
     *   cancelBtn: { text: '', touchClass: 'action-touch' }
     * }
     * @description popup  titleokBtn  cancelBtn
     *
     *  title  Popup 
     *
     *  text  touchClass  className
     */
    popupHeader: PropTypes.shape({
        title: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
        okBtn: PropTypes.shape({
            text: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
            touchClass: PropTypes.string
        }),
        cancelBtn: PropTypes.shape({
            text: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
            touchClass: PropTypes.string
        })
    }),
    /**
     * @property duration
     * @type Number
     * @default 200ms
     * @description `Popup` duration ms
     */
    duration: PropTypes.number,
    /**
     * @property pickerHeight
     * @type Number
     * @default 150
     * @description `Picker` height  Picker 
     */
    pickerHeight: PropTypes.number.isRequired,
    /**
     * @property looped
     * @type Bool
     * @default true
     * @description `Picker` looped  true
     *
     * BoolPicker
     * Picker
     */
    looped: PropTypes.oneOfType([
        PropTypes.bool,
        PropTypes.arrayOf(PropTypes.bool)
    ]),
    /**
     * @property unit
     * @type Number/String/Array
     * @default null
     * @description `Picker` unit  picker 
     *
     * NumberStringPicker
     * Picker
     */
    unit: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.arrayOf(
            PropTypes.oneOfType([
                PropTypes.number,
                PropTypes.string
            ])
        )
    ]),
    /**
     * @property popupExtraClass
     * @type String
     * @default null
     * @description  popup  class
     */
    popupExtraClass: PropTypes.string,
    /**
     * @property beforePopupShow
     * @type Function
     * @default null
     * @version 3.0.8
     * @description Popup  false Popup 
     *
     *  value  value 
     */
    beforePopupShow: PropTypes.func,
    children: PropTypes.element
};

const defaultProps = {
    touchClass: '',
    value: null,
    options: null,
    popupHeader: {
        title: '',
        okBtn: { text: '', touchClass: 'action-touch' },
        cancelBtn: { text: '', touchClass: 'action-touch' }
    },
    onChange: () => {},
    onSelect: () => {},
    pickerHeight: 150,
    duration: 200,
    looped: true,
    unit: null,
    fieldExtraClass: null,
    beforePopupShow: () => true,
    popupExtraClass: null
};

class PopupPicker extends Component {
    constructor(props) {
        super(props);
        this.state = {
            show: false,
            pickerValue: null
        };
    }

    componentWillMount() {
        const { value, options } = this.props;
        this.resetValue(value, options);
    }


    componentWillReceiveProps(nextProps) {
        if (nextProps.value != null && nextProps.value !== this.state.pickerValue) {
            this.setState({
                pickerValue: this.formatPickerProp(nextProps.value)
            });
        }
    }


    componentWillUnmount() {
        document.body.removeChild(this.wrapper);
    }

    getMultiPickerConfig({ options, looped, unit }) {
        const value = this.state.pickerValue;
        const renderedOpts = this.formatPickerOpt(options);
        const renderedLooped = this.formatPickerProp(looped);
        const renderedUnit = this.formatPickerProp(unit);

        return renderedOpts.map((optGroup, i) => ({
            options: optGroup,
            value: value[i],
            looped: renderedLooped[i] != null ? renderedLooped[i] : renderedLooped[0],
            unit: renderedUnit[i] != null ? renderedUnit[i] : renderedUnit[0]
        }));
    }
    getPickerValue(val, options) {
        const value = options.find((item) => item.value === val);
        const defValue = options[0] ? options[0].value : null;
        return value ? val : defValue;
    }

    getOnChangeValue(value, i) {
        return this.state.pickerValue.map((val, idx) => idx === i ? value : val);
    }

    resetValue(value, options) {
        if (value === null || value === undefined) {
            this.setState({
                pickerValue: this.defaultPickerValue(options)
            });
        } else {
            this.setState({
                pickerValue: this.formatPickerProp(value)
            });
        }
    }

    handleTap() {
        const { beforePopupShow } = this.props;
        let _willShow = true;
        if (isFunction(beforePopupShow)) {
            _willShow = beforePopupShow();
        }
        if (_willShow !== false) {
            this.show();
        }
    }

    show() {
        this.setState({ show: true });
    }

    hide() {
        this.setState({ show: false });
    }

    handlePopupOk() {
        const { onChange } = this.props;
        const renderedOpts = this.formatPickerOpt(this.props.options);
        const { pickerValue } = this.state;
        renderedOpts.forEach((opt, i) => {
            pickerValue[i] = this.getPickerValue(pickerValue[i], opt);
        });
        this.setState({ pickerValue });
        this.hide();
        onChange(this.state.pickerValue.length > 1 ? this.state.pickerValue : this.state.pickerValue[0]);
    }

    createWrapper() {
        this.wrapper = document.createElement('div');
        this.wrapper.className = 'this-element-is-a-tricky-hack-for-popuppicker-please-just-ignore-it';
        document.body.appendChild(this.wrapper);
    }

    defaultPickerValue(options) {
        options = this.formatPickerOpt(options);
        if (Array.isArray(options)) {
            return options.map((pickerOpt) => pickerOpt[0].value);
        }
        throw new Error('yo-popuppicker: option');
    }

    formatPickerOpt(options) {
        if (!Array.isArray(options[0])) {
            return [options];
        }
        return options;
    }

    formatPickerProp(prop) {
        return Array.isArray(prop) ? prop : [prop];
    }

    handlePopupCancel() {
        const { value, options } = this.props;
        this.hide();
        this.resetValue(value, options);
    }

    handlePickerChange(option, i) {
        const { onSelect } = this.props;
        this.setState({
            pickerValue: this.getOnChangeValue(option.value, i)
        });
        onSelect && isFunction(onSelect) && onSelect(this.state.pickerValue.length > 1 ? this.state.pickerValue : this.state.pickerValue[0]);
    }

    renderPicker() {
        const {
            popupHeader,
            duration,
            pickerHeight,
            popupExtraClass
        } = this.props;
        const pickerConfigs = this.getMultiPickerConfig(this.props);
        let okBtn = null;
        let cancelBtn = null;
        let title = null;
        if (popupHeader) {
            title = popupHeader.title || defaultProps.popupHeader.title;
            okBtn = popupHeader.okBtn || defaultProps.popupHeader.okBtn;
            cancelBtn = popupHeader.cancelBtn || defaultProps.popupHeader.cancelBtn;
        }

        return (
            <Popup
                show={this.state.show}
                duration={duration}
                extraClass={classNames(popupExtraClass, 'yo-popup yo-popup-picker')}
                onMaskTap={this.handlePopupCancel.bind(this)}
            >
                <header className="yo-header yo-header-popup-picker">
                    <span className="title">{title}</span>
                    <Touchable
                        onTap={this.handlePopupCancel.bind(this)}
                        touchClass={cancelBtn.touchClass || defaultProps.popupHeader.cancelBtn.touchClass}
                    >
                        <span className="regret">{cancelBtn.text || defaultProps.popupHeader.cancelBtn.text}</span>
                    </Touchable>
                    <Touchable
                        onTap={() => {
                            this.handlePopupOk();
                        }}
                        touchClass={okBtn.touchClass || defaultProps.popupHeader.okBtn.touchClass}
                    >
                        <div className="affirm">{okBtn.text || defaultProps.popupHeader.okBtn.text}</div>
                    </Touchable>
                </header>
                <div className="bd">
                    {pickerConfigs.map((cfg, i) => (
                        <Picker
                            key={i}
                            options={cfg.options}
                            value={cfg.value}
                            height={pickerHeight}
                            looped={cfg.looped}
                            unit={cfg.unit}
                            onChange={(option) => {
                                this.handlePickerChange(option, i);
                            }}
                        />
                    ))}
                </div>
            </Popup>
        );
    }

    render() {
        const { touchClass } = this.props;
        if (this.wrapper == null) {
            this.createWrapper();
        }
        return (
            <Fragment>
                <Touchable
                    onTap={this.handleTap.bind(this)}
                    touchClass={touchClass}
                >
                    {this.props.children}
                </Touchable>
                {ReactDOM.createPortal(this.renderPicker(), this.wrapper)}
            </Fragment>
        );
    }
}

PopupPicker.propTypes = propTypes;
PopupPicker.defaultProps = defaultProps;

export default PopupPicker;


