/**
 * @component Picker
 * @version 3.0.0
 * @description PickeriOSpicker
 *
 * :
 *
 * optionsoptions/
 * @instructions {instruInfo: ./picker.md}{instruUrl: picker.html?hideIcon}
 * @author jiao.shen
 */
import PickerCore from './PickerCore';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Scroller from '../../scroller/src/scroller';
import PickerItem from './PickerItem';
import classNames from 'classnames';

const SIZE = 1000000;
const ITEM_HEIGHT = 30;

export default class Picker extends Component {

    static propTypes = {
        /**
         * @property options
         * @type Array
         * @default null
         * @description pickeroptions{text:string,value:string}
         *
         * textoptionvalueoption(option)
         *
         * textvaluevalue/
         */
        options: PropTypes.arrayOf(
            PropTypes.shape(
                {
                    text: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
                    value: PropTypes.oneOfType([PropTypes.number, PropTypes.string]).isRequired
                }
            )
        ).isRequired,
        /**
         * @property value
         * @type Number/String
         * @default null
         * @description valueselectvalue
         *
         * valuepickeronChangevalue
         */
        value: PropTypes.oneOfType([PropTypes.number, PropTypes.string]).isRequired,
        /**
         * @property onChange
         * @type Function
         * @default null
         * @param value optionvalue
         * @description pickervaluepicker
         *
         * onChangeoptionoption
         *
         * value
         *
         * onChangevalueselect
         */
        onChange: PropTypes.func,
        /**
         * @property height
         * @type Number
         * @default 150
         * @description picker150
         *
         * PickerPicker
         */
        height: PropTypes.number.isRequired,
        /**
         * @property looped
         * @type Bool
         * @default true
         * @description true
         */
        looped: PropTypes.bool,
        /**
         * @property unit
         * @type Number
         * @default null
         * @description picker
         */
        unit: PropTypes.string,
        /**
         * @property extraClass
         * @type String
         * @default null
         * @description class
         */
        extraClass: PropTypes.string,
        /**
         * @property stopPropagation
         * @type Bool
         * @default false
         * @description false
         */
        stopPropagation: PropTypes.bool,
        /**
         * @property itemHeight
         * @version 3.0.9
         * @skip
         * @type Number
         * @default 30
         * @description Item, ,45 extraClass `yo-picker-xxx` 
         * ```
         * @include yo-picker(
         *      $name: 'xxx',
         *      $item-height: .45rem
         *  );
         * ```
         */
        itemHeight: PropTypes.number
    };

    static defaultProps = {
        value: null,
        onChange: () => {
        },
        height: 150,
        looped: true,
        unit: null,
        stopPropagation: false,
        extraClass: '',
        itemHeight: ITEM_HEIGHT
    };

    constructor(props) {
        super(props);
        const { options, value, height, looped, itemHeight } = props;
        const size = looped ? (SIZE * ITEM_HEIGHT) / itemHeight : options.length;

        this.pickerModel = new PickerCore({
            dataSource: options,
            value,
            loopedSize: size,
            containerHeight: height,
            itemHeight,
            looped
        });
        // ,state
        this.state = {
            thunks: this.pickerModel.thunks,
            height: this.pickerModel.containerHeight,
            contentHeight: this.pickerModel.contentHeight,
            visibleList: this.pickerModel.visibleList,
            offsetY: this.pickerModel.offsetY
        };
    }

    componentWillMount() {
        this.pickerModel
            .registerEventHandler('change', (visibleList) => {
                this.setState({ visibleList });
            })
            .registerEventHandler('momentumStart', (newY) => {
                if (this.scroller) { this.scroller.scrollTo(0, newY, 300); }
            })
            .registerEventHandler('resetValue', (newY, needRefresh) => {
                this.setState({ offsetY: newY });

                if (needRefresh) {
                    this.refreshOffsetY(newY);
                }
            })
            .registerEventHandler('refresh', (offsetY, visibleList, options, height, contentHeight, thunks) => {
                this.setState({ offsetY, visibleList, options, height, contentHeight, thunks });
                this.refreshOffsetY(offsetY);
                // update,refresh scroller
                if (this.scroller) {
                    setTimeout(() => {
                        this.scroller.refresh({
                            scrollerHeight: contentHeight,
                            wrapperHeight: height
                        });
                    }, 0);
                }
            });
    }

    componentDidMount() {
        if (this.scroller) {
            this.scroller.refresh({
                scrollerHeight: this.state.contentHeight,
                wrapperHeight: this.state.height
            });
        }
    }

    /**
     * value,,setValue
     * 
     * @param nextProps
     */
    componentWillReceiveProps(nextProps) {
        const { value, options, height, looped, itemHeight } = nextProps;

        if (options === this.props.options
            && height === this.state.height
            && looped === this.props.looped
            && itemHeight === this.props.itemHeight
            ) {
            if (this.props.value !== value) {
                this.pickerModel.setValue(value, true);
            }
        } else {
            this.pickerModel.refresh({
                dataSource: options,
                value,
                containerHeight: height,
                loopedSize: looped ? (SIZE * ITEM_HEIGHT) / itemHeight : options.length,
                looped,
                manually: true,
                itemHeight
            });
        }
    }

    /**
     * ,(option)onChange
     * value,,option
     * @param evt
     */
    onScrollTerminate(evt) {
        // console.log('term')
        const currentY = evt.contentOffset.y,
            destination = this.pickerModel.getScrollDestination(currentY);

        // 
        // ,,
        if (currentY !== destination.y) {
            this.pickerModel.onMomentumStart(currentY);
            return;
        }
        // value,onChangerender
        const lastStateY = this.state.offsetY,
            lastItem = this.pickerModel.getScrollDestination(lastStateY).item,
            selectedItem = destination.item;
        this.pickerModel.offsetY = currentY;
        if (selectedItem.value !== lastItem.value) {
            this.props.onChange(selectedItem);
        }
        // valuevalue
        // ,offsety
        setTimeout(() => {
            if (this.state.offsetY === lastStateY) {
                this.pickerModel.offsetY = lastStateY;
                this.refreshOffsetY(lastStateY);
            }
        }, 0);
        // isScrolling
        // timeouttap
        // 200
        setTimeout(() => {
            this.isScrolling = false;
        }, 200);
    }

    /**
     * yvisibleList
     * @param y
     */
    refreshOffsetY(y) {
        this.scroller && this.scroller.scrollTo(0, y);
        if (this.props.looped) {
            this.pickerModel.onScrollTo(y);
        }
    }

    render() {
        const { extraClass, unit, itemHeight } = this.props;
        const looped = this.pickerModel.looped;
        const { visibleList, offsetY, height, contentHeight, thunks } = this.state;
        return (
            <div className={classNames('yo-picker', extraClass)} style={{ height }}>
                <span className="mask" />
                <Scroller
                    contentOffset={{ x: 0, y: offsetY }}
                    ref={dom => {
                        this.scroller = dom;
                    }}
                    stopPropagation={this.props.stopPropagation}
                    wrapper={{ clientWidth: 0, clientHeight: height }}
                    useTransition={true}
                    onScroll={(evt) => {
                        if (looped) {
                            this.pickerModel.onScrollTo(evt.contentOffset.y);
                        }
                        this.isScrolling = true;
                    }}
                    autoRefresh={false}
                    onScrollEnd={(evt) => {
                        this.onScrollTerminate(evt);
                    }}
                    // scrollCancel:1.tap,2.tap,
                    // onScrollTerminate,optiononOptionTap(option)
                    // isScrolling
                    onScrollCancel={(evt) => {
                        if (this.isScrolling) {
                            this.onScrollTerminate(evt);
                        }
                    }}
                    onMomentumScrollBegin={(evt) => this.pickerModel.onMomentumStart(evt.param.targetY)}
                    deceleration={0.001}
                    bounceTime={looped ? 600 : 200}
                >
                    <ul
                        className={'list'}
                        style={{
                            height: contentHeight
                        }}
                    >
                        {looped ?
                            thunks.map((_, order) => {
                                const ele = visibleList.find((item) => item.order === order);
                                return ele ?
                                    <PickerItem
                                        onOptionTap={(el) => {
                                            if (!this.isScrolling && this.scroller) {
                                                this.scroller.scrollTo(0, this.pickerModel.getPositionByOpt(el), 300);
                                            }
                                        }}
                                        ele={ele}
                                        itemHeight={itemHeight}
                                        key={order}
                                        order={order}
                                    /> : null;
                            }) :
                            visibleList.map((item, i) => (
                                <PickerItem
                                    onOptionTap={(ele) => {
                                        if (!this.isScrolling && this.scroller) {
                                            this.scroller.scrollTo(0, this.pickerModel.getPositionByOpt(ele), 300);
                                        }
                                    }}
                                    ele={item}
                                    itemHeight={itemHeight}
                                    key={`notLooped_${i}`}
                                    notLooped={true}
                                />
                            ))}
                    </ul>
                </Scroller>
                {unit ? <span className="yo-select-item-tag unit">{unit}</span> : null}
            </div>
        );
    }
}



