import React, { Component } from 'react';
import PropTypes from 'prop-types';
import List from '../../list/src/list';
import SwipeMenu from '../../swipemenu/src/swipemenu';

const noop = () => {
};

export default class SwipeMenuList extends Component {

    static propTypes = {
        /**
         * @property dataSource
         * @type Array/Immutable List
         * @default null
         * @description `List`
         */
        dataSource: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.shape({
            height: PropTypes.number,
            text: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
            key: PropTypes.oneOfType([PropTypes.number, PropTypes.string])
        })), PropTypes.object]).isRequired,
        /**
         * @property uniqueKeyName
         * @type String
         * @default 'key'
         * @version 3.1.3
         * @description dataSource  key
         *
         * :`` renderItem={(item)=><p>{item.someAttr}</p>} ``
         */
        uniqueKeyName: PropTypes.string,
        /**
         * @property getMenuConfig
         * @default null
         * @version 3.0.3
         * @type Function
         * @param {Object} item 
         * @param {Number} index index
         * @description SwipeMenu
         * `SwipeMenu`
         */
        getMenuConfig: PropTypes.func.isRequired,
        /**
         * @property renderItem
         * @type Function
         * @default null
         * @param {Object} item 
         * @param {Number} index index
         * @description item()index(index)JSXstring
         * SwipeMenu
         */
        renderItem: PropTypes.func,
        /**
         * @property staticSection
         * @type Element
         * @default null
         * @version 3.0.3
         * @description Infinite
         */
        staticSection: PropTypes.element,
        /**
         * @property infinite
         * @type Bool
         * @default false
         * @description List
         */
        infinite: PropTypes.bool,
        /**
         * @property infiniteSize
         * @type Bool
         * @default 20
         * @description List
         */
        infiniteSize: PropTypes.number,
        /**
         * @property itemHeight
         * @type Number
         * @default null
         * @description List
         */
        itemHeight: PropTypes.number,
        /**
         * @property itemExtraClass
         * @type Function
         * @default "item swipemenu-list-item"
         * @param {Object} item 
         * @param {Number} index 
         * @description class/List
         */
        itemExtraClass: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
        /**
         * @property onItemTap
         * @type Function
         * @default null
         * @param {Object} item 
         * @param {Number} index 
         * @param {Array} dataSource 
         * @description itemList
         *
         * :swipemenu
         */
        onItemTap: PropTypes.func,
        /**
         * @property itemTouchClass
         * @type String/Function
         * @default item-touch
         * @param {Object} item 
         * @param {Number} index index
         * @description classNameitemExtraClass
         * @version 3.0.2
         */
        itemTouchClass: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
        /**
         * @property extraClass
         * @type String
         * @default null
         * @description Listdomclass
         */
        extraClass: PropTypes.string,
        /**
         * @property style
         * @type Object
         * @default null
         * @description 
         * @version 3.0.2
         */
        style: PropTypes.object,
        /**
         * @property usePullRefresh
         * @type Bool
         * @default false
         * @description 
         */
        usePullRefresh: PropTypes.bool,
        /**
         * 
         *
         * @property pullRefreshHeight
         * @type Number
         * @description 
         * @default 40
         */
        pullRefreshHeight: PropTypes.number,
        /**
         * 
         *
         * @property renderPullRefresh
         * @type Function
         * @returns {Element}  pullRefresh  JSX
         * @description
         *
         * 
         */
        renderPullRefresh: PropTypes.func,
        /**
         * @property onRefresh
         * @type Function
         * @default null
         * @param {Array} dataSource 
         * @description 
         */
        onRefresh: PropTypes.func,
        /**
         * @property useLoadMore
         * @type Bool
         * @default false
         * @description 
         */
        useLoadMore: PropTypes.bool,
        /**
         * 
         *
         * @property loadMoreHeight
         * @type Number
         * @description 
         * @default 40
         */
        loadMoreHeight: PropTypes.number,
        /**
         * 
         *
         * @property renderLoadMore
         * @type Function
         * @returns {Element}  loadMore  JSX
         * @description
         * 
         */
        renderLoadMore: PropTypes.func,
        /**
         * @property onLoad
         * @type Function
         * @default null
         * @param {Array} dataSource 
         * @description 
         */
        onLoad: PropTypes.func,
        /**
         * @property offsetY
         * @type Number
         * @default 0
         * @description Y
         */
        offsetY: PropTypes.number,
        /**
         * 
         *
         * @property contentInset
         * @type Number
         * @description ** bottom** iPhoneX
         * @default {bottom:0}
         * @version 3.0.13
         */
        contentInset: PropTypes.shape({
            bottom: PropTypes.number
        }),
        /**
         * @property onScroll
         * @type Function
         * @default null
         * @param {Number} offsetY y
         * @description 
         */
        onScroll: PropTypes.func,
        /**
         * @property onScrollEnd
         * @type Function
         * @param {Number} offsetY  Scroller  y 
         * @default null
         * @version 3.1.4
         * @description 
         */
        onScrollEnd: PropTypes.func,
        /**
         * @property shouldItemUpdate
         * @type Function
         * @default null
         * @param {Object} next item
         * @param {Object} now item
         * @description shouldComponentUpdaterender
         * List
         */
        shouldItemUpdate: PropTypes.func,
        /**
         * @property deceleration
         * @type Number
         * @description 0.010
         * @version 3.0.6
         */
        deceleration: PropTypes.number,
        onInfiniteAppend: PropTypes.func,
        /**
         * @property onMenuOpen
         * @type Function
         * @default ()=>{}
         * @param {Object} item 
         * @param {Number} index dataSourceindex
         * @description 
         * @version 3.0.2
         */
        onMenuOpen: PropTypes.func,
        /**
         * @property onMenuClose
         * @type Function
         * @default ()=>{}
         * @param {Object} item 
         * @param {Number} index dataSourceindex
         * @description 
         * @version 3.0.2
         */
        onMenuClose: PropTypes.func,
        /**
         * @property scrollWithoutTouchStart
         * @type Bool
         * @default false
         * @description **  **
         * scrollToscrollertouchstartscrollerdisableenable
         * touchstart
         * truescrollertouchmove
         * @version 3.0.2
         */
        scrollWithoutTouchStart: PropTypes.bool,
        /**
         * @property stickyOffset
         * @type Number
         * @default 0
         * @description staticSectiony
         * @version 3.0.6
         */
        stickyOffset: PropTypes.number
    };

    static defaultProps = {
        uniqueKeyName: 'key',
        renderItem(item) {
            return typeof item.get === 'function' ? item.get('text') : item.text;
        },
        infinite: false,
        infiniteSize: 20,
        itemHeight: null,
        itemExtraClass: 'swipemenu-list-item',
        extraClass: 'yo-list-absolute',
        onItemTap: noop,
        usePullRefresh: false,
        onRefresh: noop,
        useLoadMore: false,
        onLoad: noop,
        offsetY: 0,
        onInfiniteAppend: noop,
        onMenuOpen() {
        },
        onMenuClose() {
        },
        scrollWithoutTouchStart: false,
        staticSection: null,
        shouldItemUpdate() {
            return false;
        }
    };

    static childContextTypes = {
        swipeMenuList: PropTypes.object
    };

    constructor(props) {
        super(props);
        this.swipeMenuList = [];
        // index
        this.openIndex = -1;
        // index,/
        this.cachedOpenIndex = -1;
        this.state = {
            dataSource: this.ds,
            openIndex: this.openIndex
        };
    }

    getChildContext() {
        return { swipeMenuList: this };
    }

    /**
     * renderindexthis.cachedOpenIndex===this.openIndex
     */
    componentDidUpdate() {
        this.cachedOpenIndex = this.openIndex;
    }

    /**
     * /openIndex
     * @param index
     */
    updateOpenIndex(index) {
        // openIndex
        this.cachedOpenIndex = this.openIndex;
        // openIndex
        // this.cachedOpenIndex!==this.openIndex
        this.openIndex = index;
        this.setState({ openIndex: index });

        const { onMenuOpen, onMenuClose, dataSource } = this.props;
        const itemData = typeof dataSource.get === 'function' ? dataSource.get(index) : dataSource[index];
        if (index !== -1) {
            onMenuOpen(itemData, index);
        } else {
            const lastOpenedItemData = typeof dataSource.get === 'function' ? dataSource.get(this.cachedOpenIndex) : dataSource[this.cachedOpenIndex];
            onMenuClose(lastOpenedItemData, this.cachedOpenIndex);
        }
    }

    /**
     * @method refresh
     * @description GroupListScroller
     * @version 3.0.6
     */
    refresh() {
        if (this.list) this.list.refresh();
    }

    /**
     * @method resetLoadStatus
     * @param {Bool} hasLoadMore false onLoadMore
     * @description 
     * @version 3.0.7
     */
    resetLoadStatus(hasLoadMore) {
        if (this.list) this.list.resetLoadStatus(hasLoadMore);
    }

    /**
     * @description 
     * @method scrollTo
     * @param {Number} y y
     * @param {Number} [time] 
     */
    scrollTo(y, time) {
        if (this.list) this.list.scrollTo(y, time);
    }

    /**
     * @method stopRefreshing
     * @param {Bool} [success]  false
     * @param {Object} [config] 
     * @param {number} [config.duration]  300ms
     * @param {Function} [callback] 
     * @description  Scroller (),
     */
    stopRefreshing(success, config, callback) {
        if (this.list) {
            this.list.stopRefreshing(success, config, callback);
        }
    }

    /**
     * @method startRefreshing
     * @description 
     */
    startRefreshing() {
        if (this.list) this.list.startRefreshing();
    }

    /**
     * @method stopAnimate
     * @description 
     */
    stopAnimate() {
        if (this.list) this.list.stopAnimate();
    }

    /**
     * @description 
     * @method stopLoading
     * @param {Bool} success /
     */
    stopLoading(success) {
        if (this.list) this.list.stopLoading(success);
    }

    /**
     * 
     * Scroller
     */
    closeAll(i) {
        const swipeMenu = this.swipeMenuList[this.openIndex];
        if (swipeMenu) {
            if (i !== this.openIndex) {
                swipeMenu.close();
            }
            this.updateOpenIndex(-1);
        }
    }

    render() {
        const { renderItem, getMenuConfig, shouldItemUpdate } = this.props;

        return (
            <List
                {...this.props}
                // ,
                disabled={this.openIndex !== -1}
                directionLockThreshold={3}
                ref={(list) => {
                    if (list) this.list = list;
                }}
                // /,render
                shouldItemUpdate={(next, now) => {
                    if (this.props.infinite) {
                        return this.cachedOpenIndex !== this.openIndex || shouldItemUpdate(next, now);
                    }
                    return false;
                }}
                // 
                renderItem={(item, i) => {
                    const menuConfig = getMenuConfig(item, i);
                    const { action, disable } = menuConfig;
                    // action tap,item,i,component
                    action.forEach((actionObj) => {
                        const origTap = actionObj.onTap;
                        //  taptap,tap
                        //  binded
                        if (!origTap.binded) {
                            actionObj.onTap = (component) => {
                                origTap(item, i, component);
                            };
                            actionObj.onTap.binded = true;
                        }
                    });
                    return (
                        <SwipeMenu
                            {...menuConfig}
                            ref={(component) => {
                                if (component) {
                                    this.swipeMenuList[i] = component;
                                }
                            }}
                            disable={disable || (this.openIndex !== -1 && i !== this.openIndex)}
                            onOpen={() => this.updateOpenIndex(i)}
                            onClose={() => {
                                if (this.state.openIndex !== -1) {
                                    this.updateOpenIndex(-1);
                                }
                            }}
                            action={action}
                            extraClass="swipemenu-list-menu"
                        >
                            {renderItem(item, i)}
                        </SwipeMenu>
                    );
                }}
                onItemTap={(item, i, target) => {
                    // onItemTap
                    this.props.onItemTap(item, i, target);
                }}
                onItemTouchStart={(item, i, evt) => {
                    evt.preventDefault();

                    const currentTarget = evt.currentTarget;
                    const front = currentTarget.childNodes[0].childNodes[0];
                    const action = currentTarget.childNodes[0].childNodes[1];
                    let target = evt.target;
                    let touchInFront = target === front;
                    while (target !== front && target !== action) {
                        if (target == null) {
                            touchInFront = true;
                            break;
                        }
                        if (target.parentNode === front) {
                            touchInFront = true;
                            break;
                        }
                        if (target.parentNode === action) {
                            touchInFront = false;
                            break;
                        }
                        target = target.parentNode;
                    }

                    if (touchInFront) {
                        this.closeAll(i, evt);
                    }
                }}
            />
        );
    }
}


