/**
 * api
 * @component loading
 * @type {Object}
 * @version 3.0.0
 * @description Loading API
 *
 * @instructions {instruInfo: ./loadingapi.md}{instruUrl: loadingapi.html?hideIcon}
 * @author zongze.li
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import ReactDom from 'react-dom';
import Modal from '../../modal/src/modal';
import Loading from './loading';

const container = document.createElement('div');
document.body.appendChild(container);

let that = null;

const loadingApiPropTypes = {
    /**
     * @property text
     * @type String
     * @default ''
     * @description loading
     */
    text: PropTypes.string,
    /**
     * @property extraClass
     * @type String
     * @default ''
     * @description loadingdivclass
     */
    extraClass: PropTypes.string,
    /**
     * @property modalExtraClass
     * @type String
     * @default ''
     * @description modalclass
     */
    modalExtraClass: PropTypes.string,
    /**
     * @property show
     * @type Bool
     * @default false
     * @description loadingtrueloadingfalse
     */
    show: PropTypes.bool,
    /**
     * @property contentOffset
     * @type Array
     * @default [0,0]
     * @description /,[0,-100]100
     */
    contentOffset: PropTypes.arrayOf(PropTypes.number),
    /**
     * @property maskOffset
     * @type Array
     * @default [0,0]
     * @description ,
     *
     * ,
     */
    maskOffset: PropTypes.arrayOf(PropTypes.number)
};

const loadingApiDefaultProps = {
    text: '',
    extraClass: '',
    show: false,
    contentOffset: [0, 0],
    maskOffset: [0, 0]
};

class LoadingApi extends Component {

    constructor(props) {
        super(props);
        this.state = { ...props };
        that = this;
    }

    render() {
        const { text, modalExtraClass, extraClass, ...restProps } = this.state;
        return (
            <Modal
                align="center"
                extraClass={modalExtraClass}
                {...restProps}
            >
                <Loading
                    extraClass={extraClass}
                    text={text}
                />
            </Modal>
        );
    }
}
LoadingApi.propTypes = loadingApiPropTypes;
LoadingApi.defaultProps = loadingApiDefaultProps;

ReactDom.render(<LoadingApi />, container);

export default {
    /**
     * show
     * @method show
     * @category loading
     * @version 3.0.0
     * @param {Object} options 
     * @description apiLodingoptions
     */
    show: (options) => {
        that.setState(Object.assign({}, options, { show: true }));
    },
    /**
     * hide
     * @method hide
     * @category loading
     * @version 3.0.0
     * @description apiLoding
     */
    hide: () => {
        that.setState({ show: false });
    }
};


